<template>
  <div id="app">
    <div class="title">
      <Title />
    </div>
    <div class="city">
      <Light class="light" />
      <div>
        <li class="item disabled d1"></li>
        <li class="item disabled d2"></li>
      </div>
      <div id="zone-wrap">
      <ul class="zone" id="zone" v-on:click="handleClick">
        <li v-for="(item, index) in items" class="item" :key="index" :qr-idx="index">
          {{ item.name }}
        </li>
    </ul>
      </div>
    </div>
    <div class="qr" v-if="this.on" @click="handleClick">
      <div class="qr-img">
        <img :src="locate.url" class="qr-imgfile" alt=""/>
      </div>
      <div class="qr-city">{{this.locate.text}}</div>
      <div class="qr-note">请长按扫描二维码进群</div>
    </div>
    <footer>
      <a href="https://m.okjike.com/topics/59a99a3cd7e94000115bef8e"><Footer></Footer></a>
    </footer>
  </div>
</template>

<script>
import citys from './map.json'
import Title from './assets/title.svg'
import Footer from './assets/footer.svg'
import Light from './assets/light.svg'

export default {
  name: 'app',
  components: {
    Title,
    Footer,
    Light
  },
  data () {
    return {
      msg: '即刻影迷会空间站',
      count: 0,
      items: citys,
      on: false,
      onIndex: -1,
      lastIndex: -1
    }
  },
  computed: {
    locate: function () {
      if (this.on >= 0) {
        const item = this.items[this.onIndex]
        return {
          url: './static/img/' + item.code + '.JPG',
          text: item.name + '站'
        }
      }
    }
  },
  methods: {
    handleClick: function (e) {
      this.on = !this.on
      this.lastIndex = this.onIndex
      const item = e.target
      const index = item.getAttribute('qr-idx')
      // const zone = document.getElementById('zone')
      if (this.on) {
        this.onIndex = index
      }
      if (!this.on) {
        // zone[this.lastIndex].classList = 'item'
      }
      // if (index) {
      //   item.classList = this.on ? 'item on' : 'item'
      // }
    }
  }
}
</script>

<style>
html {
  font-size: 15px;
   -webkit-text-size-adjust:none;
}

body {
  margin: 0;
  padding: 0;
  position: relative;
}

#app {
  margin: 0 auto;
  padding-top: 40px;
  min-width: 370px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  z-index: -1;
  background-image: linear-gradient(to bottom, #081340, #0b1230);
}

ul {
  list-style-type: none;
  padding: 0;
}

a {
  color: #000;
  text-decoration: none;
}

.city {
  margin: 25px auto;
  max-width: 375px;
  position: relative;
}

.zone {
  margin-top: 0;
  /* max-height: 280px;
  overflow: scroll; */
}

#zone-wrap{
  position: relative;
  z-index: 999;
}

.light {
  position: absolute;
  left: 0;
  z-index: 1;
}

.item {
  display: inline-block;
  margin: 9px 7px;
  color: #fff;
  width: 62px;
  height: 34px;
  line-height: 34px;
  border-radius: 17px;
  border: solid 1px #fff;
}

.item:active, .item.on {
  background-image: linear-gradient(114deg, #ffe411, #ffcc11);
  box-shadow: 0 2px 16px 0 rgba(255, 208, 17, 0.45);
  color: #08133f;
}

.disabled {
  opacity: 0.5;
  margin-top: 58px;
}

.qr {
  position: fixed;
  width: 320px;
  height: 396px;
  border-radius: 29px;
  background-color: #ffffff;
  margin: 15vh auto; /* Will not center vertically and won't work in IE6/7. */
  left: 0;
  right: 0;
  top: 0;
  z-index: 9999;
}

.qr-img {
  width: 221px;
  height: 221px;
  margin: 50px auto 15px;
  overflow: hidden;
  position: relative;
}
.qr-imgfile {
  width: 108%;
  left: -4px;
  top: -65px;
  position: absolute;
  z-index: 500;
}
.qr-city {
  font-family: SourceHanSansSC;
  font-size: 29px;
  font-weight: bold;
  color: #fa4866;
}
.qr-note {
  color: #08133f;
}

footer {
  padding-bottom: 28px;
}
</style>



